
import OrdersOverview from '@/components/content/ordersoverview';
import Header from '@/components/header/page';
import React from 'react';
import {
  LineChart,
  Line,
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
} from 'recharts';

const salesData = [
  { month: 'Jan', revenue: 4000, orders: 240 },
  { month: 'Feb', revenue: 3000, orders: 139 },
  { month: 'Mar', revenue: 5000, orders: 280 },
  { month: 'Apr', revenue: 4780, orders: 350 },
  { month: 'May', revenue: 5890, orders: 390 },
  { month: 'Jun', revenue: 4390, orders: 290 },
  { month: 'Jul', revenue: 4490, orders: 310 },
];

export default function SalesStatsPage() {
  return (
    <div className="bg-gray-50 min-h-screen p-8 space-y-12">
      {/* Header Section */}
      <Header />

      <h1 className="text-4xl font-bold text-gray-900">Sales Statistics</h1>

      {/* KPI Section */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-8 mb-12">
        <div className="bg-white p-8 shadow-xl rounded-lg flex items-center justify-between">
          <div>
            <h2 className="text-xl font-medium text-gray-600">Total Revenue</h2>
            <p className="text-2xl font-semibold text-green-600">$25,000</p>
          </div>
          <div className="p-4 bg-green-100 rounded-full">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
              className="w-8 h-8 text-green-600"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M3 3v18h18V3H3z"
              />
            </svg>
          </div>
        </div>
        <div className="bg-white p-8 shadow-xl rounded-lg flex items-center justify-between">
          <div>
            <h2 className="text-xl font-medium text-gray-600">Total Orders</h2>
            <p className="text-2xl font-semibold text-blue-600">1,200</p>
          </div>
          <div className="p-4 bg-blue-100 rounded-full">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
              className="w-8 h-8 text-blue-600"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M3 3v18h18V3H3z"
              />
            </svg>
          </div>
        </div>
        <div className="bg-white p-8 shadow-xl rounded-lg flex items-center justify-between">
          <div>
            <h2 className="text-xl font-medium text-gray-600">Avg Order Value</h2>
            <p className="text-2xl font-semibold text-purple-600">$50</p>
          </div>
          <div className="p-4 bg-purple-100 rounded-full">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
              className="w-8 h-8 text-purple-600"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M3 3v18h18V3H3z"
              />
            </svg>
          </div>
        </div>
        <div className="bg-white p-8 shadow-xl rounded-lg flex items-center justify-between">
          <div>
            <h2 className="text-xl font-medium text-gray-600">Returning Customers</h2>
            <p className="text-2xl font-semibold text-yellow-600">30%</p>
          </div>
          <div className="p-4 bg-yellow-100 rounded-full">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
              className="w-8 h-8 text-yellow-600"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M3 3v18h18V3H3z"
              />
            </svg>
          </div>
        </div>
      </div>

      {/* Sales Chart Section */}
      <div className="bg-white p-8 shadow-xl rounded-lg mb-12">
        <h2 className="text-2xl font-semibold text-gray-900 mb-6">Revenue Trends</h2>
        <ResponsiveContainer width="100%" height={350}>
          <LineChart data={salesData} margin={{ top: 20, right: 30, left: 0, bottom: 5 }}>
            <CartesianGrid stroke="#e1e1e1" />
            <XAxis dataKey="month" />
            <YAxis />
            <Tooltip />
            <Line type="monotone" dataKey="revenue" stroke="#82ca9d" strokeWidth={2} />
            <Line type="monotone" dataKey="orders" stroke="#8884d8" strokeWidth={2} />
          </LineChart>
        </ResponsiveContainer>
      </div>

      {/* Recent Sales Table Section */}
      <div className="bg-white p-8 shadow-xl rounded-lg">
        <OrdersOverview />
      </div>
    </div>
  );
}
